
import { defineComponent, PropType } from 'vue'
import { getImgUrl } from '@/helpers/imageHelper';
import { IProject } from '../../data/projects';

export default defineComponent({
  name: 'ProjectItem',
  props: {
    project: {
      type: Object as PropType<IProject>,
      required: true
    }
  },
  setup() {
    return {
      getImgUrl
    }
  },
})
